* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
  color: #fff;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style-type: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}
body {
  width: 100%;
  font-family: 'Inter', sans-serif;
}
.wrapper {
  max-width: 1600px;
  margin: 0 auto;
}
.header {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  max-width: 1400px;
  height: 100px;
  z-index: 50;
}
.header__left {
  width: 120px;
  display: flex;
  justify-content: space-between;
  margin: 41px auto;
  margin-left: 0;
  font-size: calc(16px + 4 * ((100vw - 360px) / (1600 - 360)));
  font-weight: 600;
  padding-left: 10px;
}
.header__left-logo {
  margin-top: -6px;
}
.header__center-text {
  margin: auto;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  margin: 42px auto;
  width: 507px;
  margin-left: -5.8%;
  color: #18216d;
}
.header__right {
  display: flex;
  justify-content: space-between;
  width: 240px;
  margin: auto;
  margin-right: 1px;
  padding-right: 10px;
}
.header__right-text {
  font-size: calc(16px + 2 * ((100vw - 360px) / (1600 - 360)));
  font-weight: 500;
  margin: auto;
  margin-left: 18px;
  padding: 5px;
}
.header__right-button {
  min-height: 52px;
  min-width: 136px;
  border-radius: 6px;
  background-color: #18216d;
}
.best-video {
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  margin: 40px auto;
  padding: 30px 10px 10px 10px;
}
.best-video__text {
  color: #18216d;
  margin-top: 30px;
  padding: 5px 30px 5px 5px;
}
.best-video__text h1 {
  font-size: calc(28px + 20 * ((100vw - 360px) / (1600 - 360)));
  line-height: calc(27px + 33 * ((100vw - 360px) / (1600 - 360)));
  font-weight: 600;
  min-width: 498px;
  min-height: 120px;
}
.best-video__text h2 {
  font-size: calc(14px + 3 * ((100vw - 360px) / (1600 - 360)));
  line-height: calc(20px + 8 * ((100vw - 360px) / (1600 - 360)));
}
.best-video__text button {
  min-width: 180px;
  min-height: 66px;
  background: #00ecd0;
  color: #18216d;
  border-radius: 6px;
  margin-top: 34px;
  padding: 15px;
  font-size: calc(14px + 3 * ((100vw - 360px) / (1600 - 360)));
}
.best-video__picture {
  margin: -50px 0 0 -200px;
  padding: 10px;
}
.rating {
  max-width: 1150px;
  display: flex;
  justify-content: space-between;
  margin: 90px auto;
  overflow: hidden;
}
.rating__startup {
  width: 494px;
  min-height: 150px;
  font-weight: 600;
  font-size: calc(20px + 16 * ((100vw - 360px) / (1600 - 360)));
  line-height: calc(30px + 30 * ((100vw - 360px) / (1600 - 360)));
  color: #18216d;
  overflow: hidden;
  padding: 0 0 5px 5px;
}
.rating__indication {
  display: flex;
  justify-content: space-between;
  width: 300px;
  padding-left: 15px;
}
.indication__downloads {
  padding-right: 15px;
}
.logo {
  display: flex;
  justify-content: space-between;
  max-width: 1085px;
  margin: 0 auto;
  padding: 0 15px 0 15px;
}
.advantage-top {
  max-width: 542px;
  margin: 115px auto;
  color: #18216d;
  padding: 15px;
  overflow: hidden;
}
.advantage-top h2 {
  font-weight: 600;
  font-size: calc(24px + 24 * ((100vw - 360px) / (1600 - 360)));
  line-height: calc(24px + 40 * ((100vw - 360px) / (1600 - 360)));
  text-align: center;
}
.advantage-top h3 {
  font-size: calc(14px + 2 * ((100vw - 360px) / (1600 - 360)));
  line-height: calc(20px + 10 * ((100vw - 360px) / (1600 - 360)));
  min-width: 542px;
  min-height: 60px;
  text-align: center;
}
.advantage__bottom {
  display: flex;
  justify-content: space-between;
  max-width: 1150px;
  margin: -20px auto;
  padding: 15px;
}
.bottom__pricing {
  max-width: 320px;
  color: #18216d;
  padding: 10px;
  margin: -10px;
}
.bottom__pricing h2 {
  font-weight: 600;
  font-size: calc(20px + 4 * ((100vw - 360px) / (1600 - 360)));
  line-height: calc(24px + 5 * ((100vw - 360px) / (1600 - 360)));
  margin-top: 28px;
  padding: 10px;
}
.bottom__pricing h3 {
  margin: 18px 0;
  font-size: calc(14px + 2 * ((100vw - 360px) / (1600 - 360)));
  line-height: calc(24px + 2 * ((100vw - 360px) / (1600 - 360)));
  padding: 10px;
  opacity: 60%;
}
.bottom__pricing h4 {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  padding: 10px;
}

@media (max-width: 1144px) {
  .best-video {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: -60px;
    padding-left: 15%;
    padding-right: 15%;
  }
  .best-video__picture {
    margin: 0;
  }
}
@media (max-width: 992px) {
  .c-hamburder {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    margin: 0;
    padding: 0;
    width: 50px;
    height: 50px;
    cursor: pointer;
    background: #fff f;
    border: none;
    z-index: 9999;
    box-shadow: none;
    margin-top: 10px;
    font-size: 0;
  }
  .c-hamburder:focus {
    background: #005fec;
    border-radius: 3px;
    outline: none;
  }
  .c-hamburder span {
    display: block;
    position: absolute;
    top: 20px;
    left: 5px;
    right: 5px;
    height: 5px;
    border-radius: 20px;
    background: #fff f;
  }
  .c-hamburder span::before,
  .c-hamburder span::after {
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 2.31px;
    background: #273551;
    content: "";
  }
  .c-hamburder span::before {
    top: -5px;
    width: 28px;
  }
  .c-hamburder-line span::after {
    bottom: -5px;
    width: 20px;
    margin-left: 8px;
  }
  .c-hamburder-line span {
    -webkit-transition: background;
    -moz-transition: background;
    -ms-transition: background;
    -o-transition: background;
    transition: background 0s 0.3s;
  }
  .c-hamburder-line span::before,
  .c-hamburder-line span::after {
    transition-duration: 0.3s, 0.3s;
    transition-delay: 0.3s, 0s;
  }
  .c-hamburder-line span::before {
    transition-property: top, transform;
  }
  .c-hamburder-line span::after {
    transition-property: bottom, transform;
  }
  .c-hamburder-line.is-active span {
    background: none;
  }
  .c-hamburder-line.is-active span::before {
    top: 0;
    transform: rotate(-45deg);
    width: 25px;
    height: 4px;
    border-radius: 20px;
  }
  .c-hamburder-line.is-active span::after {
    bottom: 0;
    left: -6px;
    transform: rotate(45deg);
    width: 25px;
    height: 4px;
    border-radius: 20px;
  }
  .c-hamburder-line.is-active span::before,
  .c-hamburder-line.is-active span::after {
    transition-delay: 0s, 0, 3s;
  }
  .header__center-text {
    position: absolute;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    line-height: 35px;
    align-items: flex-start;
    margin-top: 100px;
    margin-left: 100px;
    width: 300px;
    transition: all ease 1s;
    width: 30px;
    height: 20px;
  }
  #menu {
    display: flex;
    text-align: left;
    width: 300px;
    margin-right: -240px;
    background: rgba(182,86,103,0);
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    right: 0;
    transition: all ease 1s;
    overflow: auto;
    z-index: 77;
  }
  #menu a {
    color: #fff;
  }
  #menu.nav-active {
    display: flex;
    background: #005fec;
    margin-right: 0;
    z-index: 77;
  }
  .header {
    height: 67px;
  }
  .header__left {
    margin: auto 0;
  }
  .header__right {
    display: none;
  }
  .best-video__text button {
    min-width: 120px;
    min-height: 40px;
  }
  .rating {
    margin: 50px auto;
    padding: 10px 15px 10px 15px;
  }
  .rating__indication {
    transform: scale(0.9);
  }
  .logo {
    transform: scale(0.8);
    width: 105%;
  }
  .advantage-top {
    margin: 50px auto;
  }
}
@media (max-width: 805px) {
  .best-video__picture {
    transform: scale(0.85);
    margin-top: -30px;
  }
  .logo {
    transform: scale(0.7);
    margin-left: -10vw;
  }
  .logo__fedex {
    display: none;
  }
  .logo__airbnb,
  .logo__hubspot,
  .logo__google,
  .logo__microsoft,
  .logo__walmart {
    padding-right: 7vw;
  }
  .rating__indication {
    transform: scale(0.8);
    margin-top: -17px;
  }
  .rating {
    margin: 0px auto;
    padding: 10px;
  }
}
@media (max-width: 675px) {
  .best-video__picture {
    transform: scale(0.7);
    margin-left: -150px;
  }
  .rating {
    margin-top: -110px;
    flex-wrap: wrap;
    justify-content: center;
  }
  .rating__indication {
    transform: scale(0.7);
    margin-top: -50px;
  }
  .logo__walmart {
    display: none;
  }
  .advantage-top h3 {
    min-width: 543px;
    opacity: 50%;
    letter-spacing: -0.02em;
    margin: 0;
    padding-right: 35px;
    margin-top: 25px;
  }
  .best-video {
    justify-content: center;
  }
}
@media (max-width: 535px) {
  .best-video__text {
    min-width: 100vw;
    padding: 0 15px 0 15px;
  }
  .best-video__text h1 {
    min-width: 100%;
  }
  .best-video__text h2 {
    margin-top: -30px;
  }
  .best-video__picture {
    margin: -130px 0 0 -40px;
  }
  .best-video__picture img {
    transform: scale(0.6);
  }
  .rating {
    margin: -160px 0 0 0;
  }
  .logo {
    transform: scale(0.5);
    display: flex;
    justify-content: center;
    padding-left: 120px;
  }
  .advantage-top h3 {
    min-width: 100vw;
  }
  .advantage__bottom {
    flex-wrap: wrap;
  }
}
